@keyframes rotate {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}

.loading {
  padding: 15px;
  
  svg {
    animation-name: rotate;
    animation-iteration-count: infinite;
    animation-duration: 1.2s;
    animation-timing-function: linear;
  }
}