@import url(https://fonts.googleapis.com/css?family=Noto+Sans&display=swap);
body {
  margin: 0;
  font-family: 'Noto Sans', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.search{padding:10px;display:flex;align-items:center;background:#FAFAFA;border-bottom:1px solid #262626}.search-app{display:flex;flex-direction:column;height:100vh}.search__field-inner{flex-grow:1;position:relative}.search__input{width:100%;border:none;font-size:16px;line-height:22px;color:#000000;background:transparent;font-family:inherit}.search__input::-webkit-input-placeholder{color:#CCC5C4}.search__input:-ms-input-placeholder{color:#CCC5C4}.search__input::placeholder{color:#CCC5C4}.search__input:active,.search__input:focus{outline:none}.search__button{padding:10px;background:transparent;border:unset;margin-right:5px;cursor:pointer}.search__button:active,.search__button:focus{outline:none}

.list{padding:8px 0;flex:1 1;position:relative}.list__item{padding:8px 16px;display:flex;align-items:center;cursor:pointer}.list__item:hover,.list__item:active,.list__item:focus{background:#F2F2F2;outline:none}.list__picture{width:40px;height:40px;border-radius:50%;overflow:hidden;margin-right:8px;border:1px solid #E5E5E6}.list__picture img{width:100%;height:100%}.list__title{font-size:14px;line-height:19px;color:#000000}.list__subtitle{font-size:12px;line-height:16px;color:#98999A}.list__loading{position:absolute;text-align:center;top:50%;transform:translateY(-50%);left:0;right:0}

@keyframes rotate{from{transform:rotate(0)}to{transform:rotate(360deg)}}.loading{padding:15px}.loading svg{animation-name:rotate;animation-iteration-count:infinite;animation-duration:1.2s;animation-timing-function:linear}

